'use strict';

import format from '@stdlib/error-tools-fmtprodmsg';

// MODULES //

import Buffer from '@stdlib/buffer-ctor';

import Float64Array from '@stdlib/array-float64';
import Float32Array from '@stdlib/array-float32';
import Int16Array from '@stdlib/array-int16';
import Int32Array from '@stdlib/array-int32';
import Int8Array from '@stdlib/array-int8';
import Uint16Array from '@stdlib/array-uint16';
import Uint32Array from '@stdlib/array-uint32';
import Uint8Array from '@stdlib/array-uint8';
import Uint8ClampedArray from '@stdlib/array-uint8c';
import Complex64Array from '@stdlib/array-complex64';
import Complex128Array from '@stdlib/array-complex128';
import BooleanArray from '@stdlib/array-bool';

// FUNCTIONS //

/**
* Throws an error.
*
* @private
* @throws {Error} not implemented
*/
function notImplemented() {
	throw new Error( format('0i900') );
}

// MAIN //

// Mapping from data types to underlying buffer constructors...
var ctors = { // eslint-disable-line vars-on-top
	'binary': Buffer,
	'float64': Float64Array,
	'float32': Float32Array,
	'float16': notImplemented, // FIXME: replace with Float16Array constructor once implemented
	'generic': Array, // TODO: replace with `stdlib` pkg
	'int16': Int16Array,
	'int32': Int32Array,
	'int8': Int8Array,
	'uint16': Uint16Array,
	'uint32': Uint32Array,
	'uint8': Uint8Array,
	'uint8c': Uint8ClampedArray,
	'complex32': notImplemented, // FIXME: replace with Complex32Array constructor once implemented
	'complex64': Complex64Array,
	'complex128': Complex128Array,
	'bool': BooleanArray
};

// EXPORTS //

export default ctors;
